 $background-color: black;
 $navbar-background-color: rgba(25, 25, 25, 0.527);
 $text-color : white;


 body {
     direction: rtl;
     background-color: $background-color;
     margin: 0;
     padding: 0;
 }

.container {
    width: 70%;
    margin: 0 auto;
}

 ul {
     position: relative;
    direction: rtl;
    list-style-type: none;
    margin: 0;
    margin-top: 40px;
    padding: 0;
    overflow: hidden;
    //background-image: url('/bgimg.png');
    background-color: $navbar-background-color;

    img {
        position: absolute;
        left: 50%;
    }
}

