li {
    list-style: none;
    // display: inline-block;
    float: right;

    
    a {
        display: block;
        color: $text-color;
        text-align: center;
        margin: 14px 0;
        padding: 0 16px;
        text-decoration: none;
        font-size: 20px;
        border: none;
    }
    a:hover {
        background-color: #111;
    }
}

 li:nth-child(1) a,
 li:nth-child(4) a {
     border-left: 1px solid white;
 }

 .logo a {
         background: url("/bgimg.png");
         background-repeat: no-repeat;
         background-position: center;
         background-size: cover;
         height: 200px;
         width: 500px;

     }